* {
  padding: 0;
  margin: 0;
  font-family:  Roboto, -apple-system, BlinkMacSystemFont, Helvetica Neue, Segoe UI, Oxygen, Ubuntu, Cantarell, Open Sans, sans-serif;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

img {
  border: 0;
  vertical-align: middle;
}

ul, ol, li {
  list-style: none;
}

a {
  text-decoration: none;
  color: #0366d6;
}

th, td {
  padding: 5px 10px;
}

.clearfix:after {
  content: " ";
  display: block;
  clear: both;
  height: 0;
}

.clearfix {
  zoom: 1;
}

/*仿BS container*/

.container {
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}

@media (min-width: 768px) {
  .container {
    width: 750px;
    /*页面宽度大于768px时盒子宽度*/
  }
}

@media (min-width: 992px) {
  .container {
    width: 970px;
    /*页面宽度大于992px时盒子宽度*/
  }
}

@media (min-width: 1200px) {
  .container {
    width: 1170px;
    /*页面宽度大于1200px时盒子宽度 		设置时注意代码顺序、执行顺序不一致将导致代码不生效 */
  }
}

/*仿BS row*/

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

/*column百分比宽度计算时不计算父容器内外边距，width百分比可以沾满一行而不溢出*/

.row {
  margin-left: -15px;
  margin-right: -15px;
}

.row:before, .row:after, .container:before, .container:after {
  display: table;
  content: " ";
}

/*解决子元素float后撑不开父元素的问题,CSS3清除浮动大法*/

.row:after, .container:after {
  clear: both;
}

/*解决子元素float后撑不开父元素的问题，CSS3清除浮动大法*/

/*仿BS column*/

.col-md-3, .col-md-4, .col-md-6, .col-xs-12 {
  position: relative;
  min-height: 1px;
  padding-right: 15px;
  padding-left: 15px;
  float: left;
}

.col-xs-12 {
  width: 100%;
}

@media (min-width: 992px) {
  .col-md-6 {
    width: 50%;
  }

  .col-md-4 {
    width: 33.3333%;
  }

  .col-md-3 {
    width: 25%;
  }
}

:after, :before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

/*公用样式*/

.text-gray {
  color: #586069;
}

.padding-10{
  padding:10px 0;
}

/*组件标题*/

.componentTitle {
  font-size: 36px;
  margin: 80px 0;
}

.titleBtn {
  padding: 5px 10px;
  text-align: center;
  display: inline-block;
  border-radius: 3px;
}

.titleBtnInside {
  color: #fff;
}

.title_p {
  font-size: 30px;
}

@media screen and (max-width: 996px) {
  .componentTitle {
    font-size: 24px;
  }

  .title_p {
    font-size: 18px;
  }
}
















































































































































































